.App {
  text-align: center;
  margin: auto;
  width: 90%;
  height: 100%;
  background: rgb(3, 102, 27);
}

.container{
  margin: auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 5px;
  justify-items: center;
  /* height: 10000px; */
      
}

.App-header {
  background-color: #1c791c;
  margin-top: 1rem;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  /* height: 10000px; */
}
.search > button {
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  height: 3rem;
  border: 3px solid black;
  border-radius: 8px;
  width: 10rem;
}
.search{
  direction: rtl;
  display: flex;
}
.search .contactButton{
  position: relative;
  right:  0px;
  background-color: rgb(14, 3, 109); 
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  height: 3rem;
  border: 3px solid black;
  border-radius: 8px;
  width: 10rem;
}
.search > form{
  align-items: center;
}
  


@media only screen and (max-width: 1200px) {
  .container {
    margin: auto;
    display: grid;
    grid-gap:5px;
    grid-template-columns: 1fr 1fr ;
    
    
}

.App {
  text-align: center;
  margin: auto;
  width: 100%;
  background: rgb(3, 102, 27);
} 
}
@media only screen and (max-width: 600px) {
.container {
  
  display: inline-block;
 /* padding-top: 2px; */
