.popupContainer{
    background-color: #184218;
    color: white;
    text-align: right;
    position: sticky;
    top: 0px;
    right: 0rem;
    padding: 1rem;
    z-index: 99;
}
.popupContainer > img{
   width: 5rem;
}

@media only screen and (max-width: 480px) {
    .popupContainer > img{
        width: 3rem;
     }

}